.App {
  text-align: center;
}

.app__form{
  padding: 20px;
  position: fixed;
  bottom: 0;
  z-index: 1;
  background-color: #e9e9eb;
  width: 50%;
  margin: 20px;
  margin-left: 25%;
  border-radius: 15px;
}

.app__logo{
  margin-top: 40px;
}

.app__formControl{
  display: flex !important;
  flex-direction: row !important;
}

.app__input{
  flex: 1;
}

.app__iconButton{
  flex: 0;
}

